import React, {useState} from 'react';
import Truncate from "react-truncate";
import { useTranslation } from 'react-i18next';
import {object} from "prop-types";
import { backendUrl } from '../../../config/config';

import './index.scss';
import min from "../../../assets/page/Rolls/min.svg";
import max from "../../../assets/page/Rolls/max.svg";
import {ACTIONS} from "../../../context/providerContext";
import {useCustomStore} from "../../../context/useStore";
import Basket from "../../../components/Basket";

const HeaderBlock = (props) => {
    const {state, dispatch} = useCustomStore();

    return (
        <div
            className={'header-slider'}
            key={props.food._id}
        >
            <div className={'header-slider_list-block'}>
                <div className={'list-content'}>
                    <div className={'list-content_header'}>
                        <h1>{props.food.name_ru}</h1>
                        <p>
                            ({
                                state.cart.basket!== undefined &&
                                state.cart.basket.find(el => el.id === props.food._id) ?
                                    state.cart.basket.map(el => {
                                        if(el.id === props.food._id){
                                            return el.countBasket
                                        }
                                    }) : 1
                            } шт.) Вес {props.food.weight} г. 
                        </p>
                        <span> {props.food.description_ru}</span>
                    </div>
                    <div className={'list-content_center'}>
                        <amp-img
                            src={min}
                            alt={'-'}
                            onClick={() => dispatch({type: ACTIONS.REMOVE_TO_BASKET, payload: props.food._id})}
                            width={'12'}
                            height={'62'}
                        >
                        </amp-img>
                        <span>
                                <p>
                                    {
                                        state.cart.basket!== undefined &&
                                        state.cart.basket.find(el => el.id === props.food._id) ?
                                            state.cart.basket.map(el => {
                                                if(el.id === props.food._id){
                                                    return el.countBasket
                                                }
                                            }) : 1
                                    }
                                </p>
                            </span>
                        <amp-img
                            src={max}
                            alt={'+'}
                            onClick={() =>  dispatch({type: ACTIONS.ADD_TO_BASKET, payload: props.food})}
                            width={'12'}
                            height={'62'}
                        >
                        </amp-img>
                    </div>
                    <div className={'list-content_footer'}>
                        <div className={'price'}>
                            <h2>
                                {props.food.percent !== 0 ? 
                                (props.food.price * ( (100 - props.food.percent) / 100)).toFixed(1) : props.food.price} 
                                грн.
                            </h2>
                            {
                                props.food.percent !== 0 && <p>{props.food.price} грн.</p>
                            }
                        </div>
                        <Basket {...props} title={'button.toOrder'} style={'open-modalTwo'} />
                    </div>
                </div>
            </div>
            <div className={'header-slider_image-block'}>
                <img
                    src={backendUrl + `/${props.food.image}`}
                    alt={props.food.name}
                />
            </div>
        </div>
    )
}

HeaderBlock.propTypes = {
    food: object.isRequired
}

export default HeaderBlock;